	height: 100%;
}

.item-overly-trans {
	.rating-stars {
		position: absolute;
		bottom: 12px;
		left: 12px;
		z-index: 2;
		background: $black-5;
		border-radius: 5px;
		padding: 5px;
	}
	a {
		position: absolute;
		bottom: 15px;
		left: 15px;
		z-index: 2;
		background: #79808a;
		color: $white;
		border-radius: 5px;
		padding: 5px;
	}
	.rating-stars .rating-stars-container .rating-star {
		&.sm {
			color: $white-5;
		}
		&.is--active {
			color: #f1c40f;
		}
	}
}

.rating-stars .rating-stars-container .rating-star.is--hover {
	color: #f1c40f;
}

.item-tag {
	h4 {
		margin: 2px;
		color: $white;
		background: $secondary6;
		padding: 5px;
		border-radius: 5px;
		float: right;
	}
	position: absolute;
	top: 30%;
	right: 13px;
	z-index: 98;
}

.item-card7-icons {
	position: absolute;
	top: 30%;
	left: 15px;
	z-index: 98;
	a {
		width: 2rem;
		display: inline-block;
		height: 2rem;
		text-align: center;
		border-radius: 100px;
		line-height: 2.1rem;
		border-radius: 50px;
		color: $white;
	}
}

.item-overly-trans .rating-stars {
	position: absolute;
	bottom: 61%;
	left: 13px;
	z-index: 2;
	background: $black-5;
	border-radius: 5px;
	padding: 5px;
}

.item-all-card {
	&::after {
		left: 0;
		bottom: 0;
		transition-duration: 0.4s;
	}
	a::after,
	&::before {
		right: 0;
		top: 0;
		transition-duration: 0.4s;
	}
	a::before {
		left: 0;
		bottom: 0;
		transition-duration: 0.4s;
	}
	&::before,
	&::after {
		content: "";
		width: 0;
		height: 2px;
		position: absolute;
		transition: all 0.4s linear;
	}
}

.item-all-cat .item-all-card a {
	&::before,
	&::after {
		content: "";
		width: 2px;
		height: 0;
		position: absolute;
		transition: all 0.4s linear;
	}
}

.item-all-card:hover {
	&::before,
	&::after {
		width: 100%;
	}
	a {
		&::before,
		&::after {
			height: 100%;
		}
	}
}

.iteam-all-icon {
	position: relative;
	width: 60px;
	margin: 0 auto;
	&:before {
		content: '';
		position: absolute;
		width: 55px;
		height: 55px;
		top: 3px;
		bottom: 0;
		border-radius: 50%;
		right: -15px;
	}
}

@media (min-width: 568px) {
	.item2-gl .item2-gl-nav .select2.select2-container.select2-container--default {
		&.select2-container--focus,
		&.select2-container--below.select2-container--open {
			width: 150px !important;
		}
	}
}

.item2-gl .item-card9 h4 {
	color: #212530;
}

